import Vue from 'vue';
export default Vue.extend({
    props: {
        value: {
            required: true
        },
        script: {
            required: true
        }
    },
    data() {
        return {
            v: this.value.default,
        };
    },
    watch: {
        v() {
            this.script.aiScript.updatePageVar(this.value.name, this.v);
            this.script.eval();
        }
    }
});
