import Vue from 'vue';
export default Vue.extend({
    props: {
        value: {
            required: true
        },
        page: {
            required: true
        },
    },
    data() {
        return {
            image: null,
        };
    },
    created() {
        this.image = this.page.attachedFiles.find(x => x.id === this.value.fileId);
    }
});
