import Vue from 'vue';
export default Vue.extend({
    props: {
        value: {
            required: true
        },
        script: {
            required: true
        }
    },
    data() {
        return {
            v: 0,
        };
    },
    watch: {
        v() {
            this.script.aiScript.updatePageVar(this.value.name, this.v);
            this.script.eval();
        }
    },
    methods: {
        click() {
            this.v = this.v + (this.value.inc || 1);
        }
    }
});
