import Vue from 'vue';
import i18n from '../../../../i18n';
export default Vue.extend({
    i18n: i18n('pages'),
    props: {
        value: {
            required: true
        },
        script: {
            required: true
        }
    },
    data() {
        return {
            text: this.script.interpolate(this.value.text),
            posted: false,
            posting: false,
        };
    },
    created() {
        this.$watch('script.vars', () => {
            this.text = this.script.interpolate(this.value.text);
        }, { deep: true });
    },
    methods: {
        post() {
            this.posting = true;
            this.$root.api('notes/create', {
                text: this.text,
            }).then(() => {
                this.posted = true;
                this.$root.dialog({
                    type: 'success',
                    splash: true
                });
            });
        }
    }
});
