import Vue from 'vue';
import i18n from '../../../../i18n';
import { faHeart as faHeartS } from '@fortawesome/free-solid-svg-icons';
import { faHeart } from '@fortawesome/free-regular-svg-icons';
import XBlock from './page.block.vue';
import { ASEvaluator } from '../../../../../../misc/aiscript/evaluator';
import { collectPageVars } from '../../../scripts/collect-page-vars';
import { url } from '../../../../config';
class Script {
    constructor(page, aiScript, onError) {
        this.page = page;
        this.aiScript = aiScript;
        this.onError = onError;
        this.eval();
    }
    eval() {
        try {
            this.vars = this.aiScript.evaluateVars();
        }
        catch (e) {
            this.onError(e);
        }
    }
    interpolate(str) {
        if (str == null)
            return null;
        return str.replace(/{(.+?)}/g, match => {
            const v = this.vars[match.slice(1, -1).trim()];
            return v == null ? 'NULL' : v.toString();
        });
    }
}
export default Vue.extend({
    i18n: i18n('pages'),
    components: {
        XBlock
    },
    props: {
        page: {
            type: Object,
            required: true
        },
        showTitle: {
            type: Boolean,
            required: false,
            default: true
        },
        showFooter: {
            type: Boolean,
            required: false,
            default: false
        },
    },
    data() {
        return {
            script: null,
            faHeartS, faHeart
        };
    },
    created() {
        const pageVars = this.getPageVars();
        this.script = new Script(this.page, new ASEvaluator(this.page.variables, pageVars, {
            randomSeed: Math.random(),
            user: this.page.user,
            visitor: this.$store.state.i,
            page: this.page,
            url: url
        }), e => {
            console.dir(e);
        });
    },
    methods: {
        getPageVars() {
            return collectPageVars(this.page.content);
        },
        like() {
            this.$root.api('pages/like', {
                pageId: this.page.id,
            }).then(() => {
                this.page.isLiked = true;
                this.page.likedCount++;
            });
        },
        unlike() {
            this.$root.api('pages/unlike', {
                pageId: this.page.id,
            }).then(() => {
                this.page.isLiked = false;
                this.page.likedCount--;
            });
        },
        pin(pin) {
            this.$root.api('i/update', {
                pinnedPageId: pin ? this.page.id : null,
            }).then(() => {
                this.$root.dialog({
                    type: 'success',
                    splash: true
                });
            });
        }
    }
});
